@use "../mixins/mixins" as mixins;

$aspect-height: 9;
$aspect-width: 16;

.embed-container {
    @include mixins.embed-container($aspect-height, $aspect-width);
    position: relative;
    height: 0;
    overflow: hidden;
    max-width: 100%;

    iframe,
    object,
    embed {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}